            <h4 style="color: indigo; font-family: 'Poppins', sans-serif; font-weight: 700; text-align: center;padding: 35px 0 0 0;">
                NMIT
                XEROX</h4>
            <div>
                <a href="#" target="_blank">
                    <p class="indigo-text"> Fill the details and Upload your file.</p>
