                        <!--                            <input type="text" name="txtPhone" class="form-control" placeholder="Your Phone Number *"-->
                        <!--                                   value=""/>-->
                        <!--                        </div>-->

                    </div>
                    <div class="col-md-6">
                        <div class="form-group">
                            <label>Admission No.</label>
                            <input class="form-control" v-model="users.admission_no" type="text" name="prenom"
                                   id="prenom" required
                                   placeholder="Eg: 17348"/>
                        </div>
                    </div>
                </div>
                <br><br>
                <div class="form-group col-md-6">
                    <div class="form-group">
                        <input id="file" type="file" @change="previewImage" accept="image/*, application/pdf"/>
                        <p style="padding: 10px 0;" id="file_size"></p>
                    </div>
                </div>

				<div class="form-group" v-if="this.imageData">
					<div class="left_column">
						<div class="form-group" style="display: flex;  margin-bottom: 10px">

							<p style="margin-right: 10px">Color<span>*</span> :</p>
							<input v-on:click="checkColor" class="with-gap" type="radio" id="color_yes" name="color_val"
								   value="Yes">
							<label style="margin-right: 15px" class="with-gap" for="color_yes">Yes</label><br>
							<input v-on:click="checkColor" class="with-gap" type="radio" id="color_no" name="color_val"
								   value="No">
							<label class="with-gap" for="color_no">No</label><br>

						</div>

						<div class="form-group" style="display: flex; margin-bottom: 10px"
