							<input v-model="users.pdf_file_props.color.color_pages" type="text" required
								   placeholder="Eg: [1-4]"/>
						</div>

						<div class="form-group" style="display: flex;  margin-bottom: 10px">

							<p style="margin-right: 10px">Double Sided <span>*</span> :</p>
							<input v-on:click="checkDoubleSided" class="with-gap" type="radio" id="double_side_yes"
								   name="double_side"
								   value="Yes">
							<label style="margin-right: 15px" class="with-gap" for="double_side_yes">Yes</label><br>
							<input v-on:click="checkDoubleSided" class="with-gap" id="double_side_no" type="radio"
								   name="double_side"
								   value="No">
							<label class="with-gap" for="double_side_no">No</label><br>

						</div>

					</div>

					<div class="right_column">
						<div class="form-group">
							<p>Number of Copies <span>*</span></p>
							<input v-model="users.pdf_file_props.number_of_copies" type="text" id="double_sided"
								   required
								   placeholder="Default: 1"/>
						</div>
					</div>


					<div>
						<div class="form-group" style="display: flex;  margin-bottom: 10px">

							<p style="margin-right: 10px">Binding <span>*</span> :</p>
							<input v-on:click="checkBinding" class="with-gap" type="radio" id="binding_yes"
								   name="binding_value"
								   value="Yes">
							<label style="margin-right: 15px" class="with-gap" for="binding_yes">Yes</label><br>
							<input v-on:click="checkBinding" class="with-gap" type="radio" id="binding_no"
								   name="binding_value"
								   value="No">
							<label class="with-gap" for="binding_no">No</label><br>
						</div>

						<div class="form-group" style="margin: 0 0 10px 30px"
							 v-if="users.pdf_file_props.binding.value === 'Yes'">

							<p style="margin-right: 10px">Soft Binding - Rs.10<span>*</span> :</p>
							<input v-on:click="checkBindingDetail" class="with-gap" type="radio" id="soft_binding_yes"
								   name="binding_val"
								   value="Yes" checked>
							<label class="with-gap" for="soft_binding_yes">Yes</label><br>
							<input v-on:click="checkBindingDetail" class="with-gap" type="radio" id="soft_binding_no"
