							<p style="margin-right: 10px">Stick File - Rs.5<span>*</span> :</p>
							<input v-on:click="checkBindingDetail" class="with-gap" type="radio" id="stick_binding_yes"
								   name="binding_val"
								   value="Yes">
							<label class="with-gap" for="stick_binding_yes">Yes</label><br>
							<input v-on:click="checkBindingDetail" class="with-gap" type="radio" id="stick_binding_no"
