                            <div class="spinner-border text-primary" role="status" id="progress_bar">
                                <span class="sr-only">Loading...</span>
                            </div>

                        </div>
                        <div class="progress">
                            <div class="progress-bar progress-bar-striped" id="progressing" role="progressbar"
