<template>
    <div>
        <div class="container">
            <div class="row">
                <div class="col-sm-9 col-md-7 col-lg-5 mx-auto">
                    <div class="jumbotron min-vh-100 m-0 d-flex flex-column justify-content-center"
                         style="background-color: #F2F2F4;">

                        <div class="card card-signin my-5">
                            <div class="card-body">
                                <h3 class="card-title text-center"
                                    style="color: indigo; font-family: 'Poppins', sans-serif"><b>NMIT XEROX</b></h3>
                                <form class="form-signin">

                                    <div id="phone_row">
                                        <span class="ph_prefix">+91 -</span>
                                        <div class="form-label-group">
                                            <input class="form-control"
                                                   v-model="phone_no" placeholder="Phone Number" id="tel" type="tel"
                                                   name="telphone" maxlength="10" required autofocus/>
                                        </div>
                                    </div>
                                    <div id="recaptcha" class="row">
                                        <div ref="recaptcha" class='col s12'></div>
                                    </div>


                                    <div class="form-label-group" id="otp_row">
                                        <span class="otp_prefix">OTP -</span>
                                        <input class="form-control" v-model="otp" id="otp" type="tel" name="otp"
                                               style="padding-left: 60px"/>
                                    </div>


                                    <div style="text-align: center;">

                                        <div class="spinner-border text-primary" role="status" id="progress_bar">
                                            <span class="sr-only">Loading...</span>
                                        </div>

                                        <div id="sendOtp_button">
                                            <button class="btn btn-lg btn-primary btn-block text-uppercase"
                                                    type="submit" v-on:click="LoginPhone">Send OTP
                                            </button>
                                        </div>
                                    </div>


                                    <!--                                    <div class="custom-control custom-checkbox mb-3">-->
                                    <!--                                        <input type="checkbox" class="custom-control-input" id="customCheck1">-->
                                    <!--                                        <label class="custom-control-label" for="customCheck1">Remember password</label>-->
                                    <!--                                    </div>-->
                                    <div class="row" style="padding: 1% 1rem; display: none" id="verifyOtp_button">
                                        <button class="btn btn-lg btn-success btn-block text-uppercase"
                                                v-on:click="OtpVerification" type="submit">VERIFY OTP
                                        </button>
                                    </div>
                                    <hr class="my-4">
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
    import firebase from "firebase";

    let somethingGlobal = '';
    let coreResult;

    export default {
        name: 'login',
        data() {
            return {
                phone_no: '',
                otp: '',
            }
        },
        methods: {
            LoginPhone: function (e) {
                e.preventDefault();

                if (this.phone_no === '') {
                    alert("Phone Number Can't Be Empty!!");
                } else if (this.phone_no.length < 10) {
                    alert("Did you know that Phone Number is 10 digit? Looks like you missed some. Check again!");
                } else {

                    document.getElementById("progress_bar").style.display = "block"

                    let prefix = '+91';
                    let prefix_phone = prefix + this.phone_no

                    console.log(prefix_phone)

                    firebase.auth().signInWithPhoneNumber(prefix_phone, this.recaptcha)
                        .then(function (confirmationResult) {
                            // SMS sent. Prompt user to type the code from the message,
                            // then sign the user in with
                            // confirmationResult.confirm(code).
                            window.confirmationResult = confirmationResult;
                            coreResult = confirmationResult;
                            alert("OTP Sent!");
                            document.getElementById("progress_bar").style.display = "none"

                            somethingGlobal = "Inside LoginPhone";
                            console.log(coreResult.toString());

                            document.getElementById("verifyOtp_button").style.display = "block";
                            document.getElementById("otp_row").style.display = "block";
                            document.getElementById("sendOtp_button").style.display = "none";
                            document.getElementById("recaptcha").style.display = "none";
                            document.getElementById("phone_row").style.display = "none";

                        }, err => {
                            alert(err.message);
                        }).catch(function (error) {
                        // Error; SMS not sent
                        console.log("Error 2: " + error);
                    });
                }
            },
            OtpVerification(e) {

                e.preventDefault();

                document.getElementById("progress_bar").style.display = "block"

                coreResult.confirm(this.otp).then(result => {
                    alert("Successful Registered!");
                    document.getElementById("progress_bar").style.display = "none"
                    let user = result.user;
                    console.log(user.uid);
                    this.$router.go(this.$router.path);

                }).catch(error => {
                    // alert(error);
                    console.log(error);
                    console.log(error);
                });

            }
        },
        mounted() {

            document.getElementById("otp_row").style.display = "none";

            somethingGlobal = "Mounting";
            console.log(somethingGlobal);
            this.recaptcha = new firebase.auth.RecaptchaVerifier(this.$refs['recaptcha']);
            this.recaptcha.render();

            // this.$recap = new firebase.auth.RecaptchaVerifier('recaptcha-container', {
            // 	'size': 'normal',
            // 	'callback': function(response) {
            // 		// reCAPTCHA solved, allow signInWithPhoneNumber.
            // 		// ...
            // 	},
            // 	'expired-callback': function() {
            // 		// Response expired. Ask user to solve reCAPTCHA again.
            // 		alert("Expired reCAPTCHA. Try again!")
            // 	}
            // });

        }
    }
</script>

